.toppost11{
    width:50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .topPostImg {
    width:100%;
  }
  .topPostImg img {
    width: 100%;
    height: 100%;
    background: transparent 0% 0% no-repeat padding-box;
    box-shadow: 0px 5px 10px #0000000d;
    border-radius: 3px;
    opacity: 1;
  }
  .toppost-num {
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .one {
    gap: 0.5rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .hr{
    border-bottom: 2px solid rgb(197, 181, 181);
    width:100%;
  }
  .toppost-info {
   padding: 0.5rem;
   text-align: left;
   font-weight: medium;
   font-size:1.2rem;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
  }
  .two h1{
      background: rgb(214, 177, 177);
  }
  .last{
    width: 100%;  
    padding:10px;
    display: flex;
  }
  .last .travel{
      text-align: left;
      font-weight:medium;
      font-size:1rem;
      letter-spacing: 0px;
      color: #404040;
      opacity: 1; 
  }
  .last .date{
  text-align: left;
  font-size:1rem;
  letter-spacing: 0.85px;
  color: #404040;
  opacity: 0.24;
  }
  @media screen and (max-width:990px){
     .toppost11{
      width:100%;
      margin-top:2rem;
     } 
     .last{
      height:3rem;
     }
  }